import React from 'react'
import './ImageView.css'
function ImageView() {
  return (
    <div>
      <div className='w-75 m-auto mt-4'>
          {/* top */}
          <div className='profile-head d-flex justify-content-between'>
              <div className="profile-name d-flex align-items-center">
                  {/* <img src="https://image.tmdb.org/t/p/original/t5zCBSB5xMDKcDqe91qahCOUYVV.jpg" width={'50px'} height={'100px'} alt="" /> */}
                  <i class="fa-solid fa-user-tie text-dark"></i>
                  <h4>Profile name</h4>
              </div>
              <div className="profile-name d-flex">
                <i class="fa-regular fa-heart text-dark border px-3 py-1 mb-2 me-3"></i>
                <button style={{height:'35px'}} className=''>
                  <a href="https://image.tmdb.org/t/p/original/t5zCBSB5xMDKcDqe91qahCOUYVV.jpg" download>Download &nbsp;<i class="fa-solid fa-circle-chevron-down text-dark"></i></a>
                </button>
              </div>
          </div>

          {/* image */}
          <div>
            <center>
              <img style={{width:'80%',height:'100%'}} src="https://image.tmdb.org/t/p/original/t5zCBSB5xMDKcDqe91qahCOUYVV.jpg" alt="" />
            </center>
          </div>
          
          {/* bottom */}
          <div className='d-flex justify-content-between mt-3'>
            <div className="d-flex">
              <h6 className='me-5'>Views</h6>
              <h6 className='me-5'>Downloads</h6>
              <h6 className='me-5'>Feature in</h6>
            </div>
            <div className="d-flex mt-2">
              <h6 className='me-3 border px-3 py-1'>Share</h6>
              <h6 className='me-3 border px-3 py-1'>Info</h6>
              <h6 className='me-3 border px-3 py-1'>...</h6>
            </div>
          </div>

          {/* publish */}
          <div>

          </div>
      </div>
    </div>
  )
}

export default ImageView