
import React, { useState } from 'react'
import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import './Header.css'
import { Link } from 'react-router-dom';

function Header() {

    const [search,setSearch]=useState('')
  
  return (
    <div>
      <Navbar style={{backgroundColor:'black'}} expand="lg" className="bg-body-tertiary">
      <Container fluid>
        <img src="https://i.pinimg.com/736x/a4/2d/4b/a42d4ba0e127ea3f62026ace6803f94d.jpg" style={{height:'50px',width:'50px'}} alt="" />
        <Navbar.Brand className='text-light' href="/">ImageHarbor</Navbar.Brand>
        <Navbar.Toggle className='text-light' aria-controls="navbarScroll" />
        <Navbar.Collapse className='text-light' id="navbarScroll">
          <Nav
            className="me-auto my-2 my-lg-0"
            style={{ maxHeight: '100px' }}
            navbarScroll
          >
            <Nav.Link className='text-light' href="/">Home</Nav.Link>
            <Nav.Link className='text-light'>Explore</Nav.Link>
          </Nav>
          <Form className="d-flex">
            <Form.Control
            style={{textTransform:'capitalize'}}
              type="search"
              placeholder="Search"
              className="me-2"
              aria-label="Search"
              onChange={(e)=>{setSearch(e.target.value)}}
            />
            <Link to={`/search/${search}`}>
              <Button variant="outline-success">Search</Button>
            </Link>
          </Form>
        </Navbar.Collapse>
      </Container>
    </Navbar>
    </div>
  )
}

export default Header
