import React, { useState } from 'react'
import axios from 'axios'
import { useEffect } from 'react'
import ImageCard from '../ImageCard/ImageCard';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './ViewAllIMages.css'
import { base_url, st_api_key } from '../../../services/url';
import Banner from '../../Pages/Banner/Banner';
import LoadingComponent from '../Loader/Loader';


function ViewAllImages() {

  const [loading,setLoading]=useState(false)
  const [photos,setPhotos]=useState({})
  const [pageNum,setPageNum]=useState(1)
 
  // api fetch
  
  const fetchData=async()=>{
      try{
        setLoading(true);
        const {data}=await axios.get(`${base_url}?page=${pageNum}&${st_api_key}`);
        setPhotos(data);
      }
      catch (error) {
        alert('no data found');
      }
      finally{
        setTimeout(() => {
          setLoading(false);
        }, 500);
      }
      
  }

  console.log(photos);
  console.log(pageNum);

  useEffect(()=>{
    fetchData()
  },[pageNum])


  return (
    <>
    <Banner/>
    <h4 className='mt-2 text-dark'>Recommendation for you</h4>
    
    {
      loading? 
      
      <div style={{width:'100%',height:'200px',marginTop:'150px'}}>
          <LoadingComponent/>
      </div>
      
      :
    

      <Row className='mt-3 mb-5'>
      { 
        photos.length>0 && photos.map((photo=>(
          
          <Col sm={1} xl={3} className='mt-3'>
          <ImageCard photo={photo}/>
          </Col>
          
        )))
      }
      </Row> 
    }
    {
      photos.length>0?
       <div className='homeBottomBar'>
        <div className="pageBottom d-flex p-3" style={{color:'white',backgroundColor:'black'}}>
           
            {
              pageNum>1 ? <h5 onClick={()=>setPageNum(pageNum-1)} className='border border-2 px-4 mx-3'>Prev</h5> :''
            }
            
                <h5 onClick={()=>setPageNum(pageNum+1)} className='border border-2 px-4 mx-3'>Next</h5>
        </div>
       </div>
      :''
    }
    </>
  )
}

export default ViewAllImages