import React, { useState, useEffect } from 'react';
import './Loader.css'
const LoadingComponent = () => {
  const [loading, setLoading] = useState(true);

  // useEffect(() => {
  //   // const timer = setTimeout(() => {
  //   //   setLoading(false);
  //   // }, 4000);

  //   // return () => clearTimeout(timer);
  // }, []);

  return (
    <div>
      {loading ? (
        // Show loading animation here (e.g., a spinner)
        <div class="loader">
        <div class="circle">
          <div class="dot"></div>
          <div class="outline"></div>
        </div>
        <div class="circle">
          <div class="dot"></div>
          <div class="outline"></div>
        </div>
        <div class="circle">
          <div class="dot"></div>
          <div class="outline"></div>
        </div>
        <div class="circle">
          <div class="dot"></div>
          <div class="outline"></div>
        </div>
      </div>
      ) : (
        // After 6 seconds, show "No data found" message
        <p class='something'>No data found</p>
      )}
    </div>
  );
};

export default LoadingComponent;
