import React, { useState } from 'react'
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import './ImageCard.css'

import Modal from 'react-bootstrap/Modal';


function ImageCard({photo}) {
  // modal
  const [lgShow, setLgShow] = useState(false);

  return (
    <>
      {/* image cards */}
      <div className="card">
            <Card style={{ width: '20rem',border:'2px solid white' }}>
            {/* <h6>${photo.id}</h6> */}
            <Card.Img variant="top" src={photo.urls.regular}/></Card>
      
            <div onClick={()=>setLgShow(true)} className="card-content">

                    <div className="content-fix">
                        <div className="avatar flex align-items-center">
                            <img src={photo.user.profile_image.medium} alt="" />
                            <h5>&nbsp;{photo.user.instagram_username}</h5>
                        </div>

                        <div className="like d-flex flex-column align-items-center">
                            <i class="fa-regular fa-heart"></i>
                            <h6>{photo.likes}</h6>
                        </div>

                        <div className="download">
                            <a href={photo.links.download} download><i style={{opacity:'0.5'}} class="fa-solid fa-download"></i></a>
                        </div>
                    </div>
            </div>
      </div>
      {/* end image cards */}

      {/* modal */}
      <Modal
        size="xl"
        show={lgShow}
        onHide={() => setLgShow(false)}
        aria-labelledby="example-modal-sizes-title-lg"
      >
        <Modal.Header closeButton>
          {/* <Modal.Title id="example-modal-sizes-title-lg">
            Large Modal
          </Modal.Title> */}
        </Modal.Header>
        <Modal.Body>
        <div className='w-75 m-auto mt-4'>
          {/* top */}
          <div style={{position:'sticky',top:'0px',backgroundColor:'white'}} className='profile-head d-flex justify-content-between'>
              <div className="profile-name d-flex align-items-center">
                  <img style={{width:'40px',height:'40px',borderRadius:'50%'}} src={photo.user.profile_image.medium} alt="" />
                  &nbsp;<h6 className='fs-5'>{photo.user.instagram_username}</h6>
              </div>
              <div className="profile-name d-flex">
                <i class="fa-regular fa-heart text-dark border px-3 py-1 mb-3 me-3"><span style={{fontSize:'8px'}}>{photo.likes}</span></i>
                <a href={photo.urls.full} download>
                  <button style={{height:'35px'}} className='mt-2 px-3'>Download</button>
                </a>
              </div>
          </div>

            <div style={{overflowY:'scroll',overflow:'hidden'}}>
              {/* image */}
              <div>
                <center>
                  <img style={{width:'80%',maxHeight:'80vh',objectFit:'contain'}} src={photo.urls.full ? photo.urls.full :'https://cdn.dribbble.com/users/1753953/screenshots/3818675/animasi-emptystate.gif'} alt="" />
                </center>
              </div>

              {/* bottom */}
              <div className='d-flex justify-content-between mt-3'>
                <div className="d-flex">
                  <div className="d-flex flex-column justify-content-center me-5 align-tems-center">
                    <h6 className=''>Views</h6>
                    {/* <p style={{fontSize:'14px',marginTop:'-10px',margin:'auto'}} className=''>123</p> */}
                  </div>
                  <div className="d-flex flex-column justify-content-center me-5 align-tems-center">
                    <h6 className=''>Downloads</h6>
                    {/* <p style={{fontSize:'14px',marginTop:'-10px',margin:'auto'}} className=''>123</p> */}
                  </div>
                  <div className="d-flex flex-column justify-content-center me-5 align-tems-center">
                    <h6 className=''>Feature in</h6>
                    {/* <p style={{fontSize:'14px',marginTop:'-10px',margin:'auto'}} className=''>123</p> */}
                  </div>
                  {/* <h6 className='me-5'>Downloads</h6>
                  <h6 className='me-5'>Feature in</h6> */}
                </div>
                <div className="d-flex mt-2">
                  <h6 className='me-3 border px-3 py-1'>Share</h6>
                  <h6 className='me-3 border px-3 py-1'>Info</h6>
                  <h6 className='me-3 border px-3 py-1'>...</h6>
                </div>
              </div>

              {/* publish */}
              <div>

              </div>
            </div>
      </div>
        </Modal.Body>
      </Modal>
      {/* end modal */}
    </>
  )
}

export default ImageCard