import React, { useEffect, useState } from 'react'
import './SearchResult.css'
import ImageCard from '../../ImageCard/ImageCard';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { Link } from 'react-router-dom';
import LoadingComponent from '../../Loader/Loader';


function SearchResult(props) {
    const [data,setData]=useState([])
    const [pageNum,setPageNum]=useState(1)
    const [isLoading, setIsLoading] = useState(true);
    const searchResults=props.searchResults;
    const name=props.name;
    console.log(props? props.loading : 'no data');
    console.log(searchResults);
    console.log(props);

    useEffect(()=>{
        if(searchResults.results)
        {
            setTimeout(()=>{
                setData(searchResults.results)
                setIsLoading(false);
            },500)
        }
    })

    // console.log(searchResults);
    
  return (
    <>
    
        <div className="main">
            <div className="top-bar">
                <div className="images">
                    <h6 className="pageBottom d-flex justify-content-center align-items-center p-3"><i style={{marginLeft:'-120px'}} class="fa-solid fa-camera">&nbsp;&nbsp;</i><span>&nbsp;Images:{searchResults.total}</span></h6>
                </div>
                <div className="pages">
                    <h6 className="pageBottom d-flex justify-content-center align-items-center p-3"><i style={{marginLeft:'-100px'}} class="fa-regular fa-file">&nbsp;&nbsp;</i><span>Pages:{searchResults.total_pages}</span></h6>
                </div>
            </div>
        </div>


        {
            isLoading || props.loading ? 
            
            <div style={{height:'300px',marginTop:'300px'}}>
                <LoadingComponent/>
            </div>
            :

        <Row>
        {
            data.length>0?
            data.map((photo)=>(
            <Col sm={1} md={2} lg={3}>
                <ImageCard photo={photo}/>
            </Col>
            ))
            :
            <img style={{width:'100%',objectFit:'contain'}} src="https://static.vecteezy.com/system/resources/previews/004/968/529/original/search-no-results-found-concept-illustration-flat-design-eps10-simple-modern-graphic-element-for-landing-page-empty-state-ui-infographic-icon-with-editable-stroke-line-outline-linear-vector.jpg" alt="" />
            
        }
        </Row>
        }

        {
        data.length>0?
       <div className='homeBottomBar'>
        <div className="pageBottom d-flex p-3" style={{color:'white',backgroundColor:'black'}}>

          
           
            {
                
               pageNum>1 ? <h5 onClick={()=>setPageNum(pageNum-1)} className='border border-2 px-4 mx-3'>
                <Link to={`/search/${name}/${pageNum}`}>
                    Prev   
                </Link></h5> :''
            }
            
                <h5 onClick={()=>setPageNum(pageNum+1)} className='border border-2 px-4 mx-3'>
                    
                <Link to={`/search/${name}/${pageNum}`}>
                    Next   
                </Link>
                </h5>
        </div>
       </div>
    
      :''
    }
       
    </>

   
  )
}

export default SearchResult