import React, { useEffect, useState } from 'react';
import axios from 'axios';
import './Search.css'
import { searchImage, st_api_key } from '../../../../services/url';
import SearchResult from '../SearchResult/SearchResult';
import { useParams } from 'react-router-dom';



function Search() {
  const [search, setSearch] = useState('Cars');
  const [searchData, setSearchData] = useState({});
  const pageNumber=useParams();
  const [loading,setLoding]=useState(false)
  const {name}=useParams()
  
  
  const searchFetch = async() => {
    try {
      setLoding(true);
      const response=await axios.get(`${searchImage}?query=${search}&page=${pageNumber.id}&${st_api_key}`)
      setSearchData(response.data);
    } catch (error) {
      console.log(error);
    }
    setTimeout(()=>{
      setLoding(false)
    },400)
  };

 
    useEffect(()=>{
      if(name)
      {
       setSearch(name);
      }
    },[name])
  

  useEffect(() => {
      searchFetch();
  }, [search]);
  
  useEffect(() => {
    searchFetch()
}, [pageNumber]);// Trigger the searchFetch when 'search' state changes




  return (
    <>
    
      <div className="main">
        <div className='search-lists d-flex justify-content-center'>
           <h5 onClick={()=>setSearch('Shoes')} className='border border-danger px-5 py-2 m-2'>Shoes</h5>
           <h5 onClick={()=>setSearch('Nature')} className='border border-danger px-5 py-2 m-2'>Nature</h5>
           <h5 onClick={()=>setSearch('Cars')} className=' border border-danger px-5 py-2 m-2'>Cars</h5>
           <h5 onClick={()=>setSearch('Toys')} className=' border border-danger px-5 py-2 m-2'>Toys</h5>
           <h5 onClick={()=>setSearch('Ships')} className=' border border-danger px-5 py-2 m-2'>Ships</h5>
           <h5 onClick={()=>setSearch('Birds')} className=' border border-danger px-5 py-2 m-2'>Birds</h5>
           <h5 onClick={()=>setSearch('Adventure')} className='border border-danger px-5 py-2 m-2'>Adventure</h5>
        </div>

        <div className="results mt-3">
          <h3 style={{color:'black'}}>{search}</h3>
        </div>

      </div>

      {
        <SearchResult name={name} searchResults={searchData} loading={loading} />
      }
    </>
  );
}

export default Search;
